
import {defineComponent} from 'vue';
import {useRoute} from 'vue-router';

export default defineComponent({
  name: 'NetMusic',

  setup() {
    return {
      tabs: (useRoute().matched[0].children as unknown as
          Array<{ path: string, meta: { title: string } }>)
          .filter(route => route.meta)
    };
  }

});
